    name: `Eugenija imknait`,
    description: `Jei tiksime, kad ateiname  em tam, kad j pakeistume ir paliktume dar graesn, o alia esantiems tapt lengviau gyventi dl ms buvimo alia, tai habil. Dr. Eugenijos imknaits gyvenimas bt pats tikriausias to pavyzdys. Ji buvo tarsi uuovja ir ramstis savo kartai, kuri atradus sunku prarasti. Vienas bdingiausi Jos bruo  meile grindiama globa gyvajai gamtai: sprogstaniam pumpurui ir besiskleidianiam iedui, skruzdei ir iogui. Patarimais, pamokymais, draudimais ir pagraudenimais ji stengsi keisti tobuliausi krin  mog. Nerauk, neskink, neplk, mogau, esi tos paios gamtos dalis, juk neilgai diaugsies: nuvys ir numesi. tai toki ties mok [...]. Vaistaols buvo didioji ir nuolatin jos gyvenimo meil. J painimui, tyrimui ir gausinimui paskyr vis gyvenim 
(Birut Karnickien. Eugenijos imknaits labdaros ir paramos fondo pirminink).  

Daktar Eugenija imknait buvo labai domi, savita ir nepakartojama asmenyb, mokslinink ir ypa didel vaistini augal ir liaudies medicinos inov. Savo gyvenimu ir veikla sukaup ne tik didiul etnokultrin bei intelektualin turt Lietuvai, bet ir paliko neidildomus spdius su ja bendravusiems monms bei suformavo unikal savo, kaip liaudies iniuons vaizd.
Gim 1920 m. kovo 11 d. Rusijoje, Krasnodaro krate, Novorosijsko mieste. Kartu su kitais pabgliais i Rusijos su eima grs  Lietuv tvas Pranas imknas gimtuosiuose Tauragnuose steig vaistin. Jau bdama eeri met, Eugenija imknait m pratintis prie vaistins darbo ir vaistaoli rinkimo. Nuo maens Eugenija buvo labai ingeidi. Turjo guv prot, viskuo domjosi, mgo klausytis pasakojim apie senov, paproius, olynus. Senos kaimo olininks ir kaimo senoliai, matydami Eugenijos pomgius, j mokino vaistini augal painimo ir paruoimo gydymui meno, ukeikim ir adjim, mok olynais gydyti mones, o ji dmiai vis klaussi. 
Baigusi Tauragn pradios mokykl pradjo mokytis Utenos gimnazijoje, buvo gabi, mokslai seksi. Eugenija sakydavo, kad jos tvai buvo labai protingi ir todl j aukldami nevert mokytis ko nors atmintinai: nei eilrai, nei daugybos lentels. Taip ji imoko visk mokytis apgalvodama, isiaikindama dalyko esm, o smegenyse liko vietos visiems jos gyvenime reikalingiems dalykams. 
stojusi  Kauno Vytauto Didiojo universiteto Medicinos fakulteto farmacijos skyri pasirinko tvo profesij, ruosi bti vaistininke. Karo pradioje dirbo Kauno, Tauragn vaistinse, 1942 m. atvyko  Vilni ir pradjo dirbti universiteto Botanikos sode. Praus karui botanikos sodas buvo sunaikintas, Eugenija dirbo Vilniaus 1-osios ligonins vaistinje, vliau perjo dirbti  universitet. 
1949m. atvaiavo  Kaun ir dirboKauno botanikos sodejaunesnija moksline bendradarbe, msi disertacijos, augino ir tyr valerijon kultras. Kiek vliau mokslinio darbo tsti gro  Vilni,  Moksl akademijos Biologijos institut, dirbo Akademijos Botanikos sodo direktore. Apgynusi disertacij gavo  biologijos moksl kandidats laipsn. 1955m. ivaiavo Kazachstandirbti vaistini augal kio Darmina padalinio stoties direktore, domjosi vidurins Azijos vaistiniais augalais, io krato liaudies medicina ir gyventoj paproiais, organizavo ekspedicijas laukini vaistini augal tyrimui. 
Grusi  Lietuv sidarbino vyriausioje farmacijos valdyboje vyr. inspektore vaistaoli paruooms, steigus atskir vaistaoli skyri paskirta jo virininke. Tuo metu jai suteikta aukiausia provizors farmacinio darbo organizators kvalifikacin kategorija.
1971 m. E. imknait Vilniaus universitete apgyn biologijos moksl daktars disertacij "Lietuvos vaistingj augal resurs naudojimo biologiniai pagrindai". Tai pirmoji ir turbt vienintel disertacija, kurioje nagrinjami Lietuvos vaistini augal resursai.
1993m. Lietuvos Mokslo Taryba E. imknaitei suteik habilituotos gamtos moksl daktars mokslin laipsn.
Vis savo gyvenim dr. E. imknait paskyr vaistini augal, liaudies medicinos, kratotyros tyrimui. Para daug knyg, mokslini ir mokslo populiarinimo straipsni, skait praneimus mokslinse farmacinink ir botanik konferencijose, dalyvaudavo botanik ekspedicijose. Visuomenei skirt praneim klaussi pilnos auditorijos moni, kurie po kelias valandas jos nepaleisdavo, daug klausindavo. Tyrindama vaistaoli augimo vietas Eugenija imknait ivaikiojo Lietuv, surinko tkstanius odi lietuvi kalbos odynui. Domjosi ir rinko pasakas, padavimus, dainas ir sutartines. Ji buvo senovs mogus, jai gamta buvo neatskiriama gyvenimo dalis, ji suprato augal ir gyvn kalb, pasakos jai buvo ms protvi gyvenimo ir paproi iraika, senose dainose ji skait paslpt prasm, ms tv tikjimo ir gyvenimo atspind. 
Aminai ydk, Atminimo vynia: Knyga apie Eugenij.- Vilnius: uvdra, 2006. `,
